
import wx from 'weixin-js-sdk'
import Picker from 'vux/src/components/picker/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import {mapGetters,mapActions} from 'vuex'
export default {
data(){
return{
isFrsh:false,
ey:0,
sy:0,
spTime:0,
nore:'',
show1:false,
menus1:[[]],
slected:[''],
spatime:'',
}
},
computed:{
...mapGetters(['getorderTel']),
fpData(){
let ls = {}
this.getorderTel.orderAttrList.forEach( v=>{
ls = {...ls,...v}
})
return ls
}
},
created(){
window.nore = null
if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}
this.showLoading()
this.myOrderDetail({this:this,method:this.myOrderDetail,data:{orderId:this.$route.query.id}})
},
components:{
Picker ,Confirm , LoadMore
},
methods:{
...mapActions(['myOrderDetail','cancelOrder','orderCancelReason','deleteOrder','confirmReceipt']),
ontouchstart(e){
this.sy = e.changedTouches[0].pageY
},
ontouchend(e){
this.ey = e.changedTouches[0].pageY
if(this.ey - this.sy > 50){//下拉
this.reFresh()
}
},
reFresh(){
if(!this.isFrsh){
this.isFrsh = true
this.showLoading()
this.myOrderDetail({this:this,method:this.myOrderDetail,data:{orderId:this.$route.query.id},frsh:true})
}
},
gogod(id){
wx.miniProgram.navigateTo({
url:'/pages/goodDetail/index?id='+id
})
},
shoumy(id){
this.$router.push(`/refund?csi=${id}&id=${this.$route.query.id}`)



},
lijifk(obj){
wx.miniProgram.navigateTo({
url:'/pages/payGd/index?id='+obj.orderId+'&s='+obj.saleOrderTypeId+'&m='+obj.orderSubTotal
})
},
copy(){
this.myOrderDetail({this:this,method:this.myOrderDetail,data:{orderId:this.$route.query.id}})
},
sureRe(id){
this.showLoading()
this.confirmReceipt({data:{orderId:id},this:this,method:this.confirmReceipt})
},
goCe(){
let data={
orderId:this.$route.query.id,
operateReason:this.slected[0]
}
console.log(this.slected)
this.cancelOrder({data:data,this:this})
this.show1 = false
},
cancelOrd(){
this.orderCancelReason({this:this})
this.show1 = true
},
setRe(data){
data = data.map(v => {
return {name:v.description,value:v.description}
})
this.menus1 = [data]
},
hxcz(){
this.showLoading()
this.myOrderDetail({this:this,method:this.myOrderDetail,data:{orderId:this.$route.query.id},fg:1})
},
onConfirm(){
this.deleteOrder({data:{orderId:this.$route.query.id},this:this,method:this.deleteOrder})
},
delOd(){
let _this = this
this.$vux.confirm.show({
content: '确认删除订单',
onCancel () {
},
onConfirm () {
_this.onConfirm()
}
})
},
goBack(){
this.$router.replace('/myOrder')



},
timeh(c){
let _this =this
this.spTime = parseInt(c.diffTime)||0
console.log(this.spTime)
console.log(typeof  this.spTime)
let time,h,m,s
window.nore = setInterval(()=>{
time = parseInt(this.spTime)||0
console.log(time)
if(time <= 0){
this.myOrderDetail({this:this,data:{orderId:this.$route.query.id}})
return
}
s =time%60
time =parseInt(time/60)
m = time%60
time =parseInt(time/60)
h=time%24
this.spatime= h+'时'+m+'分'+s+'秒'
this.spTime=parseInt(this.spTime) -1
console.log(this.spTime)
},1000)
},
choseWhy(k){
console.log(k);
}
},
beforeDestroy(){
clearInterval(window.nore)
},
mounted(){
document.title = '订单详情'
}
}



